@import url('https://fonts.googleapis.com/css2?family=Dosis&family=League+Spartan&family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');

:root {
    --bg-dark:linear-gradient(to bottom right, #000000, #18272b);

    --logo-height: 80px;
    --header-height:90px;
    --aside-width: 225px;

    --shadow:
        0 2px 23px 0 rgba(0, 0, 0, 0.1),
        0 2px 49px 0 rgba(0, 0, 0, 0.06);
}

* {
    box-sizing: border-box;
    width: auto;
    height: auto;
    margin: 0;
    padding: 0;
}

main.main {
    overflow: auto; /* Adiciona barras de rolagem apenas se necessário */
    min-width: 0; /* Garante que os elementos não causem estouro na largura */
    min-height: 0; /* Evita que o conteúdo force o crescimento vertical */
}

.app {
    margin: 0px;
    display: grid;
    grid-template-columns: 0.7fr 1.3fr;
    grid-template-rows: 
        var(--header-height) 1fr;
    grid-template-areas: 
        "logo nav"
        "header main";
    height: 100vh;
    background: var(--bg-dark);

    max-width: 100vw; /* Impede que a largura da aplicação ultrapasse a largura da janela */
    max-height: 100vh; /* Limita a altura total da aplicação */
    overflow: hidden; /* Oculta qualquer conteúdo que ultrapasse os limites */
}

aside.logo {
    grid-area: logo;
    border-bottom: 5px solid var(--bg-dark);
}

header.Header {
    grid-area: header;
}

aside.nav {
    grid-area: nav;
}

main.main {
    grid-area: main;
}

@media(max-width: 1024px) {
    .app {
        grid-template-columns: 0.5fr 1.5fr;
        grid-template-rows: 
            var(--header-height) 1fr;
    }
}

@media(max-width: 768px) {
    .app {
        grid-template-columns: 1fr;
        grid-template-rows: 
            auto auto 1fr;
        grid-template-areas: 
            "nav"
            "header"
            "main";
    }
}