.header {
    display: flex;
    justify-content: center;
    align-items: flex-start; 
    height: auto; 
    margin-top: 0; 
}

header img {
    margin-top: 19vh; 
    margin-left: 5vw; 
    padding: 0px;
    width: 20vw;
    height: 20vw;
    object-fit: cover;
    max-width: 500px;
    max-height: 500px;
    border-radius: 50%;
}

@media (max-width: 1024px) {
    header img {
        width: 25vw;
        height: 25vw;
        margin-top: 7vh; 
        margin-left: 7vw; 
    }   
}

@media (max-width: 728px) {
    .header {
        margin: 0px;
        padding: 0px;
        top: 0;
    }
    header img {
        width: 40vw; 
        height: 40vw; 
        top: 0;
        padding-top: 0vh;
        margin-left: 30vw;
        margin-top: 8vh; 
    }   
}