aside.logo {
    display: flex;
    justify-content: center; 
    align-items: center;
}

aside.logo img {
    padding: 0px 10px; 
    width: 20vw; 
}

@media(max-width: 1024px) {
    aside.logo img {
        width: 45vw; 
    }
}

@media(max-width: 768px) {
    aside.logo {
        display: none;
    }
    
    aside.logo img {
        display: none;
    }
}