@import url('https://fonts.googleapis.com/css2?family=Dosis&family=League+Spartan:wght@100..900&display=swap');

*{margin: 0; padding: 0;}

.nav {
    display:flex;
    justify-content: end;
    align-items: center;
    list-style:none;
    margin-right: 3vw;
    position: relative;
    width: 100%;
}

.nav li {
    position:relative;
    display: flex;
    justify-content: end;
    float:left;
}

.nav li a {
    color:#55c8c8;
    padding:1vw 1.5vw;
    text-decoration: none;
    display:block;
    font-family: "League Spartan", serif;
    font-optical-sizing: auto;
    font-weight: 700;
    font-style: normal;
    margin-right: 1vw;
    width: 100%;
    text-align: center;
}

.nav li a:hover{
    background: #55c8c8;
    color: #fff;
    border-radius: 10px;
    transition-duration: 1.8s;
}

.nav li ul{
    position:absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    list-style: none;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); 
    opacity: 0;
    visibility: hidden;
    transition: opacity 1s ease, visibility 1s ease;
}

.nav li:hover ul, .menu li.over ul{
    opacity: 1;
    visibility: visible;
}

.nav li ul li{
    width: 100%;
}

.nav li ul li a{
    font-weight: 500;
    text-align: center;
    background-color: #31313127;
    color: #309191;
}

.nav li ul li a:hover {
    background: #55c8c8;
    color: #fff;
    font-weight: 700;
    transition-duration: 1s;
}

@media(max-width: 768px) {
    .nav {
        top: 0;
        padding-top: 5vh ;
        margin: 0;
        justify-content: center;
        align-items: center; 
        background-color: #31313170;
        max-height: 7vh;
        border-bottom: 1px solid #2c6868b9;
        border-top: 1px solid #2c6868b9;
    }

    .nav li{
        margin: 0vh 3vw 5vh 3vw;
        margin-right: 0;
        max-height: 4vh;
    }

    .nav li a {
        font-size: 1.2rem;
        color: #379797;
    }

    .nav li ul{
        position:absolute;
        top: 110%;
        left: 50%;
        transform: translateX(-60%);
        font-size: 1.2rem;
        padding-top: 2vh;
    }

    .nav li ul li {
        max-height: 2vh;
        height: 100%;
    }

    .nav li ul a {
        padding: 0vh 1vw;
        height: 100%;
    }
}