h1 {
    color: #55c8c8;
    font-family: "Merriweather", serif;
    font-weight: 500;
    font-size: 3.4rem;   
}

h2 {
    color: #fff;
    font-family: "Dosis", serif;
    font-optical-sizing: auto;
    font-weight: 900;
    font-style: normal;
    font-size: 2.2rem;
}

p{
    color: #fff;
    font-family: "Dosis", serif;
    font-optical-sizing: auto;
    font-weight: 500;
    font-style: normal;
    font-size: 1.8rem;
    margin-left: 5vw;
}

.content{
    display: block; 
    margin-top: 25vh;
    margin-left: 0vw;
}

.sobre p{
    margin-top: 1vw;
    font-size: 2.0rem;
}

@media(max-width: 1024px) {
    h1 {
        font-size: 3rem;
    }

    h2 {
        font-size: 1.9rem;
    }

    p {
        font-size: 1.2rem;
    }

    .content{
        margin-top: 17vh;
        margin-left: 0; 
    }

    .sobre p{
        font-size: 1.2rem;
    }

}

@media(max-width: 728px) {
    h1 {
        font-size: 2.5rem;
    }

    h2 {
        font-size: 1.5rem;
    }

    p {
        font-size: 1.1rem;
        margin-left: 0;
    }

    .content{
        margin-top: 8vh;
        margin-left: 3vw;
    }

    .sobre p{
        font-size: 1.3rem;
        margin-top: 1vh;
        padding: 0; 
    }

}